<template>
  <div class="menu">
    <div>
      <p>
        <router-link to="/">
          <span><i class="fas fa-house-user homepageicon"></i></span>
        </router-link>
      </p>
    </div>
    <div id="list" ref="menuList">
      <ul>
        <li v-for="v of menuTree" :key="v.t">
          <router-link
            v-if="v.subCtl.length === 0"
            :to="v.t"
            tag="h3"
            class="pagelink"
            active-class="menuListLinked"
            :data-a="v.t"
          >
            {{ v.t }}
          </router-link>
          <template v-else>
            <h3 @click="showSubMenuSwitch">
              {{ v.t }}
            </h3>
            <ul>
              <li v-for="v of v.subCtl" :key="v.t">
                <router-link
                  v-if="v.subCtl.length === 0"
                  :to="'/ifr/' + v.t"
                  tag="h3"
                  class="pageLink"
                  active-class="menuListLinked"
                  :data-a="v.t"
                >
                  {{ v.t }}
                </router-link>
                <template v-else>
                  <h3 @click="showSubMenuSwitch">
                    {{ v.t }}
                  </h3>
                  <ul>
                    <li v-for="v of v.subCtl" :key="v.t">
                      <router-link
                        v-if="v.subCtl.length === 0"
                        :to="'/ifr/' + v.t"
                        tag="h3"
                        class="pageLink"
                        active-class="menuListLinked"
                        :data-a="v.t"
                      >
                        {{ v.t }}
                      </router-link>
                      <template v-else>
                        <h3 @click="showSubMenuSwitch">
                          {{ v.t }}
                        </h3>
                        <ul></ul>
                      </template>
                    </li>
                  </ul>
                </template>
              </li>
            </ul>
          </template>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      menuTree: [
        {
          t: "Javascript",
          subCtl: [
            {
              t: "Evenet - 事件模型",
              subCtl: [],
            },
            {
              t: "构造函数&原型&类",
              subCtl: [],
            },
            {
              t: "原型链 - 图示",
              subCtl: [],
            },
            {
              t: "异步设计-setTimeout&Promise&async&await",
              subCtl: [],
            },
            {
              t: "Promise",
              subCtl: [],
            },
            {
              t: "产生器函数",
              subCtl: [],
            },
            {
              t: "ES6模块化 Module",
              subCtl: [],
            },
            {
              t: "自定义基础模块管理",
              subCtl: [],
            },
            {
              t: "Meta-programming",
              subCtl: [],
            },
            {
              t: "Web组件-JS原生组件化开发",
              subCtl: [],
            },
            {
              t: "常用内置对象及其api",
              subCtl: [
                {
                  t: "内置对象简介",
                  subCtl: [],
                },
                {
                  t: "Array",
                  subCtl: [],
                },
                {
                  t: "Object",
                  subCtl: [],
                },
                {
                  t: "String",
                  subCtl: [],
                },
                {
                  t: "单列内置对象-Global",
                  subCtl: [],
                },
                {
                  t: "单列内置对象-Data",
                  subCtl: [],
                },
                {
                  t: "单列内置对象-Math",
                  subCtl: [],
                },
              ],
            },
            {
              t: "防抖和节流",
              subCtl: [],
            },
            {
              t: "事件对象-mouse事件-坐标&定位相关属性",
              subCtl: [],
            },
            {
              t: "JS-待归类",
              subCtl: [],
            },
          ],
        },
        {
          t: "HTML5 & CSS3",
          subCtl: [
            {
              t: "BFC",
              subCtl: [],
            },
            {
              t: "CSS超出文字显示省略",
              subCtl: [],
            },
            {
              t: "布局-响应式和自适应",
              subCtl: [],
            },
            {
              t: "单位PX及移动web视口",
              subCtl: [],
            },
            {
              t: "CSS自定义属性-设置变量",
              subCtl: [],
            },
            {
              t: "单位百分比的具体分析",
              subCtl: [],
            },
          ],
        },
        {
          t: "NodeJS & NPM",
          subCtl: [
            {
              t: "简介",
              subCtl: [],
            },
            {
              t: "前段工程化开发-1",
              subCtl: [],
            },
            {
              t: "前段工程化开发-2",
              subCtl: [],
            },
            {
              t: "npm包的安装",
              subCtl: [],
            },
            {
              t: "webpack",
              subCtl: [
                {
                  t: "Webpack 简介",
                  subCtl: [],
                },
                {
                  t: "vue& vue-loader &webpack 模板",
                  subCtl: [],
                },
                {
                  t: "webpack 与 module ",
                  subCtl: [],
                },
              ],
            },
            {
              t: "vue和vue-cli",
              subCtl: [],
            },
          ],
        },
        {
          t: "Vue",
          subCtl: [
            {
              t: "vue-入门",
              subCtl: [],
            },
            {
              t: "vue归纳",
              subCtl: [],
            },
          ],
        },
        {
          t: "其他",
          subCtl: [
            {
              t: "正则匹配",
              subCtl: [],
            },
          ],
        },
      ],
    };
  },
  methods: {
    showSubMenuSwitch(e) {
      e.target.nextElementSibling.classList.toggle("subMenuIsShow");
    },
  },
  mounted() {},
};
</script>
<style lang="less" Scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.menu {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  scrollbar-color: var(--emphasize-color) var(--nv-bg-color);
  scrollbar-width: thin;
  &::-webkit-scrollbar {
    width: 3px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 1em;
    background-color: rgba(50, 50, 50, 0.3);
  }
  &::-webkit-scrollbar-track {
    border-radius: 1em;
    background-color: var(--nv-bg-color);
  }
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 50px;
  #list {
    width: 92%;
    ul,
    li {
      margin: 0;
      padding: 0;
      list-style: none;
    }

    ul ul {
      display: none;
      padding-left: 20px !important;
    }

    ul li > h3,
    .h3Link {
      user-select: none;
      cursor: pointer;
      position: relative;
      font-weight: 600;
      font-size: 16px;
      word-break: break-all;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      display: block;
      width: fit-content;
      width: 100%;
      color: #333;
      border-radius: 3px;
      padding: 3px 0;
      margin: 2px 0;

      &:hover {
        background-color: var(--hover-theme-color);
      }

      &::before {
        font-family: "Font Awesome 5 Free";
        font-weight: 600;
        content: "\f0da";
        font-size: 20px;
        display: inline-block;
        width: 20px;
        height: 20px;
        transform-origin: center center;
        color: var(--emphasize-color-2);
        text-align: center;
        position: relative;
        transition: 0.2s all ease-in-out;
        margin-right: 6px;
      }
    }
  }
}
.subMenuIsShow {
  display: block !important;
}
.h3-showed {
  &::before {
    color: #333 !important;
    transform: rotateZ(90deg);
    top: 50%;
  }
}
.pageLink {
  color: #666 !important;
  font-weight: 400 !important;
}
.pageLink:hover {
  background-color: transparent !important;
  text-decoration: underline;
  text-decoration-style: solid;
  text-decoration-color: var(--emphasize-color-2);
}
.pageLink::before {
  content: "\f15c" !important;
  font-size: 14px !important;
}
.menuListLinked {
  text-decoration: underline;
  text-decoration-style: solid;
  text-decoration-color: var(--emphasize-color-2);
  text-decoration-thickness: 3px;
}
.homepageicon {
  color: var(--emphasize-color-2);
  font-size: 30px;
  padding-bottom: 10px;
  padding-top: 10px;
  transition: 0.3s all;
}
.homepageicon:hover {
  transform: scale(1.2);
}
</style>


