
import Vue from 'vue'
import App from './App'
import VueRouter from "vue-router"
import axios from 'axios'
import VueAxios from 'vue-axios'

//引入路由配置
import router from "./router"
Vue.config.productionTip = false
//应用插件
Vue.use(VueRouter)
Vue.use(VueAxios, axios)

new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router: router
})



