<template>
  <div class="container">
    <nav :style="{ width: finallyNavWidth + 'px' }" :class="navClass">
      <NavMenu></NavMenu>
      <div class="drag" @mousedown="dragdowm"></div>
      <div :class="mbuttonClass" @click="mbuttonHandle"></div>
    </nav>
    <div :class="fillClass" :style="{ width: finallyNavWidth + 'px' }"></div>
    <section>
      <router-view></router-view>
    </section>
  </div>
</template>

<script>
import "./assets/basic.css";
import "./assets/all.css";
import NavMenu from "./components/nav-menu";

export default {
  name: "App",
  components: {
    NavMenu,
  },
  data() {
    return {
      navWidth: 300,
      isDrag: false,
      isNavShow: true,
      mbuttonClass: ["mbutton"],
      navClass: [],
      fillClass: ["fill"],
      ifrDom: {},
      tE: {},
    };
  },
  computed: {
    finallyNavWidth() {
      return this.navWidth;
    },
  },

  methods: {
    dragdowm(e) {
      this.isDrag = "true";
      let drag = (e) => {
        if (this.isDrag) {
          this.navWidth = e.pageX;
        }
      };

      document.addEventListener("mousemove", drag);
      document.addEventListener("mouseup", (e) => {
        if (this.isDrag) {
          this.isDrag = false;
          document.removeEventListener("mousemove", drag);
        }
      });
      this.ifrDom.contentWindow.addEventListener("mouseup", (e) => {
        if (this.isDrag) {
          this.isDrag = false;
          document.removeEventListener("mousemove", drag);
        }
      });
    },
    getIfrDOM(dom) {
      this.ifrDom = dom;
    },
    mbuttonHandle() {
      if (this.isNavShow) {
        this.mbuttonClass.push("mbutton-show");
        this.navClass.push("nav-hidden");
        this.fillClass.push("fill-hidden");
        this.isNavShow = false;
      } else {
        this.isNavShow = true;
        this.mbuttonClass.pop();
        this.navClass.pop();
        this.fillClass.pop();
      }
    },
  },
  mounted() {
    window.addEventListener("load", () => {
      if (this.$route.params.page) {
        let linkedEle = document.querySelector(
          `h3[data-a="${this.$route.params.page}"]`
        );
        this.tE = linkedEle;
        linkedEle.classList.toggle("menuListLinked");

        if (linkedEle.parentNode.parentNode instanceof HTMLUListElement) {
          linkedEle.parentNode.parentNode.classList.toggle("subMenuIsShow");
        }

        if (
          linkedEle.parentNode.parentNode.parentNode.parentNode instanceof
          HTMLUListElement
        ) {
          linkedEle.parentNode.parentNode.parentNode.parentNode.classList.toggle(
            "subMenuIsShow"
          );
        }
      }
    });
  },
  beforeUpdate() {
    this, (ifrDom = null);
  },
  updated() {
    this.tE.classList.remove("menuListLinked");
  },
};
</script>
<style>
:root {
  --nav-max-width: 600px;
  --nav-min-width: 230px;
  --basic-theme-color: #f7f6f3;
  --nav-bg-color: #f7f6f3;
  --hover-theme-color: #e8e7e4;
  --emphasize-color: #999793;
  --emphasize-color-2: #a5a4a0;
  --transtion-during: 0.3s;
}
.container {
  position: absolute;
  width: 100%;
  min-height: 100vh;
  display: flex;
}
nav {
  width: 30%;
  min-height: 100vh;
  height: 100vh;
  position: fixed;
  max-width: var(--nav-max-width);
  min-width: var(--nav-min-width);
  background-color: var(--nav-bg-color);
  z-index: 999;
  transition: transform var(--transtion-during) ease-in-out;
  /* 给滚动条留宽度 */
  padding-right: 6px;
}

nav.nav-hidden {
  transform: translateX(-100%);
}
nav .drag {
  position: absolute;
  top: 0;
  right: 0;
  width: 5px;
  height: 100vh;
  background-color: var(--basic-theme-color);
  transition: background 0.6s ease-in-out, transform 0.6s ease-in-out;
  user-select: none;
}
nav .drag:hover {
  cursor: col-resize;
  transform: scale(1.2);
  background-color: var(--hover-theme-color);
}
nav .mbutton {
  width: 50px;
  height: 50px;
  position: absolute;
  right: 50px;
  bottom: 50px;
  border-radius: 50%;
  background-color: var(--hover-theme-color);
  transition: all var(--transtion-during) ease-in-out;
  border: 2px #fff solid;
  box-shadow: 1px 2px 3px 0 #3336;
}
nav .mbutton::after {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f00D";
  display: block;
  height: 100%;
  width: 100%;
  position: relative;
  text-align: center;
  line-height: 50px;
  font-size: 26px;
  color: var(--emphasize-color);
}
nav .mbutton.mbutton-show {
  right: -100px;
  border-radius: 10px;
}
nav .mbutton.mbutton-show::after {
  content: "\f0c9";
  font-size: 24px;
}
nav .mbutton:hover {
  background-color: #dad9d6;
  cursor: pointer;
}
.fill {
  width: 30%;
  height: 1px;
  z-index: -1;
  position: relative;
  max-width: var(--nav-max-width);
  min-width: var(--nav-min-width);
  transition: all var(--transtion-during) ease-in-out;
}

.fill.fill-hidden {
  width: 0px !important;
  min-width: 0px;
}
section {
  position: relative;
  flex: 1 1 0;
  height: 100vh;
}

@media screen and (max-width: 500px) {
  nav {
    width: 320px !important;
    height: 100vh;
    position: fixed;
    background-color: var(--nav-bg-color);
    z-index: 999;
    transition: transform var(--transtion-during) ease-in-out;
    transform: translateX(-100%);
  }

  nav.nav-hidden {
    transform: translateX(0%);
  }

  nav .mbutton {
    border-radius: 10px;
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
    right: -50px;
    background-color: var(--hover-theme-color);
    transition: all var(--transtion-during) ease-in-out;
    border: 2px #fff solid;
    box-shadow: 1px 2px 3px 0 #3336;
  }
  nav .mbutton::after {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\f0c9";
    display: block;
    height: 100%;
    width: 100%;
    position: relative;
    text-align: center;
    line-height: 30px;
    font-size: 16px;
    color: var(--emphasize-color);
  }

  nav .mbutton.mbutton-show {
    right: 20px;
    border-radius: 50%;
  }
  nav .mbutton.mbutton-show::after {
    content: "\f00D";
    font-size: 16px;
  }

  .fill {
    display: none;
  }
  nav .drag {
    display: none;
  }
}
</style>




