import VueRouter from "vue-router"
import Ifr from "../components/ifr"
import HomePage from "../components/HomePage"


//引入组件


//创建一个路由器
export default new VueRouter({
    routes: [

        {
            path: '/ifr/:page',
            component: Ifr,
        },
        {
            path: '/',
            component: HomePage,
        },


    ],
    linkActiveClass: 'aaa bbb'
})


