<template>
  <div class="ifr">
    <iframe
      @load="ifronload"
      :src="ifrURL"
      frameborder="0"
      ref="ifr"
      scrolling="no"
      id="ifr"
      class="ifrTrue"
    ></iframe>
  </div>
</template>
<script>
export default {
  name: "ifr",
  data() {
    return {};
  },
  computed: {
    ifrURL() {
      return `../../static/ifrs/${this.$route.params.page}.html`;
    },
  },

  methods: {
    ifronload(e) {
      this.$emit("getIfrDOM", this.$refs.ifr);
      // this.$refs.ifr.height = this.$refs.ifr.contentDocument.body.scrollHeight;
      window.document.title =
        "Eesa' Blog - " + this.$refs.ifr.contentDocument.title;
      // alert(ifr.contentDocument.body.scrollHeight);
      this.$refs.ifr.height = this.$refs.ifr.contentDocument.body.scrollHeight;
    },
  },
  mounted() {},
  beforeUpdate() {
    // alert(3);
  },
  updated() {
    // alert(2);
  },
};
</script>
<style Scoped>
.ifr {
  padding-top: 50px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ifr iframe {
  width: 80%;
}
</style>


